	module.exports = {"data":{"page":{"id":"C:/Users/Greg/_dev/mynpms/react-custom-share/docs/content/pages/1--share-button/index.md absPath of file >>> MarkdownRemark","html":"<h2>Demo</h2>\n<p><a href=\"../live-share-button\">Live demo</a></p>\n<h2>Description</h2>\n<p><code class=\"language-text\">ShareButton</code> is a core <code class=\"language-text\">react-custom-share</code>’s component.</p>\n<p>It renders a HTML button tag with an <code class=\"language-text\">onclick</code> attribute pointing to the fuction which opens the ‘share’ pop-up window.</p>\n<h2>Usage</h2>\n<p>Let’s create a share button for Facebook.</p>\n<p><strong>Step 1.</strong> In this example, as an inner element of the button, we are going to use a svg icon provided by <a href=\"https://github.com/react-icons/react-icons\">react-icons</a>. But you are free to use a bitmap image or text, anything what meets your needs.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">import</span> FaFacebook <span class=\"token keyword\">from</span> <span class=\"token string\">\"react-icons/lib/fa/facebook\"</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p><strong>Step 2.</strong> Import the button component.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> ShareButton <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">\"react-custom-share\"</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p><strong>Step 3.</strong> Prepare an object with props for the <code class=\"language-text\">ShareBlock</code> component. The <code class=\"language-text\">url</code> and <code class=\"language-text\">network</code> are required props.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">const</span> shareButtonProps <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  url<span class=\"token punctuation\">:</span> <span class=\"token string\">\"https://github.com/greglobinski/react-custom-share\"</span><span class=\"token punctuation\">,</span>\n  network<span class=\"token punctuation\">:</span> <span class=\"token string\">\"Facebook\"</span><span class=\"token punctuation\">,</span>\n  text<span class=\"token punctuation\">:</span> <span class=\"token string\">\"Give it a try - react-custom-share component\"</span><span class=\"token punctuation\">,</span>\n  longtext<span class=\"token punctuation\">:</span>\n    <span class=\"token string\">\"Social sharing buttons for React. Use one of the build-in themes or create a custom one from the scratch.\"</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p><strong>Step 4.</strong> Add <code class=\"language-text\">ShareButton</code> to your app.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token operator\">&lt;</span>ShareButton <span class=\"token punctuation\">{</span><span class=\"token operator\">...</span>shareButtonProps<span class=\"token punctuation\">}</span><span class=\"token operator\">></span>\n  <span class=\"token operator\">&lt;</span>FaFacebook <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n<span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>ShareButton<span class=\"token operator\">></span></code></pre>\n      </div>\n<p><strong>Full code</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">import</span> React <span class=\"token keyword\">from</span> <span class=\"token string\">\"react\"</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> ReactDOM <span class=\"token keyword\">from</span> <span class=\"token string\">\"react-dom\"</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> FaFacebook <span class=\"token keyword\">from</span> <span class=\"token string\">\"react-icons/lib/fa/facebook\"</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> ShareButton <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">\"react-custom-share\"</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> props <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> shareButtonProps <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n    url<span class=\"token punctuation\">:</span> <span class=\"token string\">\"https://github.com/greglobinski/react-custom-share\"</span><span class=\"token punctuation\">,</span>\n    network<span class=\"token punctuation\">:</span> <span class=\"token string\">\"Facebook\"</span><span class=\"token punctuation\">,</span>\n    text<span class=\"token punctuation\">:</span> <span class=\"token string\">\"Give it a try - react-custom-share component\"</span><span class=\"token punctuation\">,</span>\n    longtext<span class=\"token punctuation\">:</span>\n      <span class=\"token string\">\"Social sharing buttons for React. Use one of the build-in themes or create a custom one from the scratch.\"</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">return</span> <span class=\"token operator\">&lt;</span>ShareButton <span class=\"token punctuation\">{</span><span class=\"token operator\">...</span>shareButtonProps<span class=\"token punctuation\">}</span><span class=\"token operator\">></span>\n    <span class=\"token operator\">&lt;</span>FaFacebook <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n  <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>ShareButton<span class=\"token operator\">></span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n\nReactDOM<span class=\"token punctuation\">.</span><span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token operator\">&lt;</span>App <span class=\"token operator\">/</span><span class=\"token operator\">></span><span class=\"token punctuation\">,</span> document<span class=\"token punctuation\">.</span><span class=\"token function\">getElementById</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"root\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<h2>ShareButton’s props</h2>\n<ul>\n<li><strong>network</strong> - a network’s name | possible values: [‘Facebook’, ‘GooglePlus’, ‘Twitter’, ‘Linkedin’, ‘Pinterest’, ‘Email’] | <em>string</em> | <strong>required</strong></li>\n<li><strong>url</strong> - URL of the page to share | <em>string</em> | <strong>required</strong></li>\n<li><strong>children</strong> - an inner element of the button | <em>string</em>, <em>html tag</em> or <em>React component</em> | <strong>required</strong></li>\n<li><strong>media</strong> - URL of an image | <strong>required</strong> if network’s value is ‘Pinterest’</li>\n<li><strong>link</strong> - a link to open in the ‘share’ pop-up window, this prop is used to set a share button for networks not suported by the built-in link builder script</li>\n<li><strong>text</strong> - a short text shared together with the url | <em>string</em></li>\n<li><strong>longtext</strong> - an extension of the text shared together with the url | <em>string</em></li>\n</ul>","frontmatter":{"title":"<ShareButton />"}}},"pathContext":{"slug":"/share-button/"}}

